@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap);
body, html {
    font-family: 'Comic Neue', cursive;
}

button {
    font-family: 'Comic Neue', cursive;
}

input {
    font-family: 'Comic Neue', cursive;
}

.YNbuttons {
    display: flex;
    flex-direction: row;
}

.userContainer {
    display: flex;
    flex-flow: wrap;
}

.textBoxStyle {
    position: absolute;
    background-color: greenyellow;
    color: black;
    height: 20px;
    padding-left: 10px;
    padding-right: 10px;
}

.submitStyle {
    position: absolute;
    background-color: greenyellow;
    border-color: green;
}

.popUpIndex {
    padding: 10% 5%;
    position: absolute;
    background-color: white;
    border: solid black;
    left: 30%;
    top: 30%;
    display: flex;
    flex-direction: column;
}

@media (max-width: 1300px) {
    .userContainer { flex-direction: column;}
}
.boundingBoxUserStyle {
    position: absolute;
    border-width: 15px;
    border: solid rgb(252, 8, 183);
    box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.5);
}

.boundingBoxUserStyle:hover {
    opacity: 50%;
    background-color: rgb(252, 8, 183);
}

.boundingBoxWizardStyle {
    position: absolute;
    border-width: 15px;
    border: solid rgb(252, 8, 183);
}

.boundingBoxDeleteStyle {
    position: absolute;
    border-width: 15px;
    border: solid red;
}

.boundingBoxDeleteStyle:hover {
    opacity: 50%;
    background-color: red;
}

.textInputStyle {
    position: absolute;
    background-color: rgb(252, 8, 183);
    color: black;
    height: 20px;
    width: 100px;
}

.textBoxStyle {
    position: absolute;
    background-color: rgb(252, 8, 183);
    color: black;
    height: 20px;
    width: -webkit-max-content;
    width: max-content;
    padding-left: 10px;
    padding-right: 10px;
}

.textBoxDeleteStyle {
    position: absolute;
    background-color: red;
    color: black;
    height: 20px;
    padding-left: 10px;
    padding-right: 10px;
}

.submitStyle {
    position: absolute;
    background-color: rgb(252, 8, 183);
    border-color: pink;
    top: 26px;
    left: 2px;
}

.submitStyle:hover {
    background-color: pink;
}
.vocabBoard {
    display: flex;
    flex-direction: column;
    flex: 1 1;
}

.wordPlayer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    border: solid black;
    margin-bottom: 10px;
    min-height: 92px;
}

.contextualWords {
    min-height: 92px;
    width: auto;
    display: flex;
    flex-flow: wrap;
    border: solid black;
    margin-bottom: 10px;
}

.coreWords {
    display: flex;
    height: 1000px;
    flex-flow: column wrap;
    border: solid black;
}

button {
    margin: 1%;
}

.currentWordButton {
    height: 80px;
    min-width: 65px;
    justify-content: center;
    align-items: center;
    border-width: 3px;
    border-radius: 20%;
    display: flex;
    flex-direction: column;
}

.wordButton {
    height: 80px;
    min-width: 65px;
    justify-content: center;
    align-items: center;
    border-width: 3px;
    border-radius: 20%;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.5);
}

.wordButton:hover {
    opacity: 50%;
}

.wordButtonEditMode {
    height: 80px;
    min-width: 65px;
    justify-content: center;
    align-items: center;
    border: solid red;
    border-width: 3px;
    border-radius: 20%;
    display: flex;
    flex-direction: column;
}

.popUp {
    padding: 10% 5%;
    position: absolute;
    background-color: white;
    border: solid black;
    left: 60%;
    top: 40%;
    display: flex;
    flex-direction: column;
}

.wordButtonEditMode:hover {
    opacity: 50%;
    background-color: red;
}

.wordOnly {
    font-size: 24px;
    font-weight: bold;
}

.buttonImage {
    height: 50px;
    margin-top: 5px;
    margin-bottom: 5px;
    width: 50px;
}

.buttonImage:hover {
    opacity: 50%;
}

.colorKeyLabels {
    font-size: x-large;
    font-weight: bold;
    padding: 30px 10px;
}

@media (max-width: 1500px) {
    .coreWords {height: 1200px;}
    .popUp {left: 30%;}
}
.noImage {
    width: 700px;
    height: 500px;
    text-align: center;
}

.noImageTxt {
    padding-top: 250px;
}
.wizardImage {
    position: relative;
}

.noImageWizard {
    height: 500px;
    text-align: center;
    min-width: 50%
}
